<script>
	import {
		mapState,
		mapMutations,
		mapActions
	} from 'vuex';
	export default {
		onLaunch: function() {
			//调试使用，清理缓存
			// uni.clearStorage();
			this.getNavHeight();
			this.getStoreLoginInfo();
			this.getiPhoneX();
			this.$store.dispatch("getMarketMap");
			this.init();
			this.$store.dispatch('getToken',{first: false});
			uni.loadFontFace({
			  family: 'THS',
			  source: 'url("https://i.thsi.cn/m/fonts/THSMoneyfont-Medium.ttf")',
			  success() {
			  }
			})
		},
		globalData: {
			userInfo: null
		},
		onShow: function(opts) {
			if(!this.sysInfo.rheight) {
				this.getNavHeight();
			}
			this.$store.commit('setScene', opts.scene);
		},
		onHide: function() {
		},
		computed: {
			...mapState(['oldMarketMap', 'newMarketMap','sysInfo','loginInfo']),
		},
		methods: {
			...mapActions['getMarketMap'],
			getiPhoneX() {
				wx.getSystemInfo({
					success: res => {
						let model = res.model.replace(/\s/g, '').toLocaleLowerCase();
						let sys = res.system.replace(/\s/g, '').toLocaleLowerCase();
						let xList = ['iphonex', 'iphonrx'];
				
						if (xList.some(item => model.match(item))) {
							this.globalData.isX = true;
							this.$store.commit('setiPhoneX', true);
						}
				
						if (sys.match('ios')) {
							this.globalData.isIOS = true;
						}
						const modelTwo = res.model;
						if (/iphone\sx/i.test(modelTwo) ||
							(/iphone/i.test(modelTwo) && /unknown/.test(modelTwo)) ||
							/iphone\s11/i.test(modelTwo)) {
							this.globalData.isX = true;
							this.$store.commit('setiPhoneX', true);
						}
					},
					fail(err) {
						console.log(err);
						this.getiPhoneX();
					}
				});
			},
			getNavHeight() {
				let that = this;
				this.getSysInfo();
			},
			getSysInfo() {
				let that = this;
				let res = uni.getSystemInfoSync();
				this.formatSysInfo(res);
			},
			formatSysInfo(data) {
				if (wx.getMenuButtonBoundingClientRect) {
					let menu = wx.getMenuButtonBoundingClientRect(); //获取获取菜单按钮（右上角胶囊按钮）的布局位置信息。坐标信息以屏幕左上角为原点。（top表示上边框到手机顶部的距离 bottom是下边框到手机顶部的距离）
					let diff = 8;
					if(data.statusBarHeight) {
						diff = menu.top - data.statusBarHeight; //获取差距
					}
					let meunLeft = data.windowWidth - menu.left - (menu.width / 4);
					let height = data.statusBarHeight + diff * 2 + 32; //计算导航高度 状态栏 间距×2 胶囊按钮高度
					let cheight = diff * 2 + 32; //导航内容高度，去掉状态栏高度
					this.$store.commit("setSysInfo", {
						rheight: height,
						cheight: cheight,
						meunLeft: meunLeft,
						windowHeight: data.windowHeight
					});
				}
				if(data.platform == 'devtools') {
					this.$store.commit("setPlatform", 'pc');
				} else if(data.platform == 'ios') {
					this.$store.commit("setPlatform", 'ios');
				} else if(data.platform == 'android') {
					this.$store.commit("setPlatform", 'android');
				}
			},
			getUserLoginInfo(data) {
				let that = this;
				this.$store.dispatch('getToken',{first: true}).then().catch(()=>{
					setTimeout(()=>{
						this.getUserLoginInfo();
					},500)
				})
			},
			getStoreLoginInfo() {
				let userLoginInfo = uni.getStorageSync('loginInfo');
				if (userLoginInfo) {
					//检查session_key是否过期
					let data = JSON.parse(userLoginInfo);
					this.checkSession(data);
				} else {
					//重新获取loginInfo信息
					this.getUserLoginInfo();
				}
			},
			checkSession(data) {
				let that = this;
				wx.checkSession({
					success() {
						//session_key 未过期，并且在本生命周期一直有效
						that.$store.commit("setLoginInfo", data);
					},
					fail() {
						// session_key 已经失效，需要重新执行登录流程
						that.getUserLoginInfo(data);
					}
				})
			},
			init() {
				let guidance = uni.getStorageSync('guidance');
				if(!guidance) {
					this.$store.commit("setShowGuidance", true);
				}
			}
		}
	};
</script>

<style>
	/* 解决头条小程序组件内引入字体不生效的问题 */
	/* #ifdef MP-TOUTIAO */
	@font-face {
		font-family: uniicons;
		src: url('/static/uni.ttf');
	}

	/* #endif */
	body {
		color: #323232;
		/* font-family: 'THS'; */
	}
</style>
